<template>
  <div>

    <!--  银行机构信息样本-->
    <!--  {{this.tmpCaption}}-->
    <!--  {{this.pri_detail.treeNodeOrgId}}-->
    <el-card shadow="always" :body-style="cardBodyStyle" style="background-color: #f2f3f8"
      class="insitute-card1 insitute-card2">
      <el-breadcrumb separator-class="el-icon-arrow-right" style="padding:5px; font-weight: bold">

        <!--      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>-->
        <el-breadcrumb-item v-for="(item, index)  in this.tmpCaption " :key="index">{{ item }}
        </el-breadcrumb-item>

      </el-breadcrumb>
    </el-card>
    <el-button style="float:right; background-color:#409eff; color: #ffffff" @click.native.stop="handleAddRow()">
      添加下属机构
    </el-button>
    <!--页面展示-->
    <div>
      <el-table border class="new-table-fixed-right new-table-fixed" ref="multipleTable" :data="tableData"
        tooltip-effect="dark" @sort-change="sortColumn" :row-style="{'height':'10px'}"
        :cell-style="{'padding':'5px 0px'}" @selection-change="handleSelectionChange"
        :header-cell-style="{'background': '#4B86BC','color':'#ffffff','borderRight': '1px solid #E3E5EB' ,'padding':'0px','height':'45px','line-height':'45px'}">
        <el-table-column fixed align="center" type="index" label="序号" width="50">
        </el-table-column>
        <el-table-column prop="organization_name" label="机构名称" align="center" show-overflow-tooltip width="150">

        </el-table-column>

        <el-table-column prop="organization_code" label="机构编号" align="center" show-overflow-tooltip width="150">
          <template slot-scope="scope">
            {{scope.row.organization_code|format}}
          </template>
        </el-table-column>
        <el-table-column prop="organization_address" label="机构地址" align="center" show-overflow-tooltip width="150">
        </el-table-column>
        <el-table-column prop="organization_contacts" label="联系人" align="center" show-overflow-tooltip width="150">
        </el-table-column>
        <el-table-column prop="organization_phone" label="联系电话" align="center" show-overflow-tooltip width="150">
        </el-table-column>
        <el-table-column prop="organization_department" label="联系人所在部门" align="center" show-overflow-tooltip
          width="120">
        </el-table-column>
        <el-table-column align="left" fixed="right" label="操作" min-width="350" header-align="center">
          <template slot-scope="scope">
            <el-button style="margin-left: 10px;" @click.native.stop="handleEditRow(scope.$index, scope.row)" type="text" size="small">编辑</el-button>
            <el-button @click.native.stop="handleDeleteRow(scope.$index, scope.row)" type="text" size="small">删除</el-button>
            <el-button v-if="scope.row.organization_type==='1'" @click.native.stop="applets_configuration(scope.$index, scope.row)" type="text" size="small">小程序配置</el-button>
            <el-button v-if="scope.row.organization_type==='1'" @click.native.stop="wechat_pay(scope.$index, scope.row)" type="text" size="small">微信支付配置</el-button>
            <el-button v-if="scope.row.organization_type==='1'" @click.native.stop="share_background(scope.$index, scope.row)" type="text" size="small">小程序分享背景</el-button>
            <el-button v-if="scope.row.organization_type==='5'" @click.native.stop="unionPay_payment(scope.$index, scope.row)" type="text" size="small">银联支付配置</el-button>
            <el-button v-if="scope.row.organization_type==='5'" @click.native.stop="qr_code(scope.$index, scope.row)" type="text" size="small">银行二维码</el-button></el-button>
            <el-button v-if="scope.row.organization_type==='5'" @click.native.stop="qr_code_shop(scope.$index, scope.row)" type="text" size="small">商户二维码</el-button>
            <el-button v-if="scope.row.organization_type==='5'" @click.native.stop="qr_code_user(scope.$index, scope.row)" type="text" size="small">用户二维码</el-button>
            <el-button v-if="scope.row.organization_type==='5'" @click.native.stop="franchised_merchant(scope.$index, scope.row)" type="text" size="small">加盟商户来源管理</el-button>
            <el-button v-if="scope.row.organization_type==='5'" @click.native.stop="newlyAddedClick(scope.$index, scope.row)" type="text" size="small">新增默认渠道商</el-button>
          </template>
        </el-table-column>

        <el-table-column prop="bank_user_total" label="所有银行用户总数" align="center" show-overflow-tooltip width="150">
        </el-table-column>
        <el-table-column prop="company_total2" label="下属机构总数" align="center" show-overflow-tooltip width="120">
        </el-table-column>
        <el-table-column prop="device_total" label="所有设备总数" align="center" show-overflow-tooltip width="120">
        </el-table-column>
      </el-table>
    </div>
    <div>

      <!--page start 页码-->
      <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange"
        :current-page="queryParam.currentPage" :page-sizes="[10, 20, 50, 100,200]" :page-size="queryParam.pageSize"
        layout="total, sizes, prev, pager, next, jumper" :total="queryParam.totalRow">
      </el-pagination>
      <!--page end-->
      <!-- 热力公司新增  dialog -->
      <el-dialog :visible.sync="companyDialogFormVisible" :close-on-press-escape="false" :close-on-click-modal="false"
        :show-close="false">
        <div slot="title" style="text-align: left;font-size:20px;font-weight: bold;">
          <span>{{ this.tableDialgTitle }}</span>
        </div>
        <!--form start-->
        <el-form class="account-formitem" label-position="left" :model="companyForm" :rules="companyFormRules"
          ref="companyFormRef" label-width="130px" style="text-align: left;font-size:20px;font-weight: bold;">

          <el-row :gutter="24">
            <el-col :md="12">
              <el-form-item label="当前机构名称:" prop="pingtai">
                <el-tag effect="dark" size="medium">
                  {{ this.pingtai }}
                </el-tag>
              </el-form-item>
            </el-col>
            <el-col :md="12">
              <el-form-item label="银行类型:" label-width="130px" v-if="bankType" prop="bci_bank_type"
                :rules="this.companyFormRules.bci_bank_type">
                <!--                :filter-method="dataFilter"-->
                <el-select v-model="value1" filterable placeholder="请选择银行类型">
                  <el-option v-for="item in cities" :key="item.value" :label="item.label" :value="item.value">
                    <span style="float: left">{{ item.label }}</span>
                    <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
                  </el-option>
                </el-select>
              </el-form-item>

            </el-col>
          </el-row>
          <el-row :md="24">
            <el-col :md="12">
              <el-form-item label="机构名称:" prop="bci_name" :rules="this.companyFormRules.bci_name">
                <el-input style="width: 220px" v-model="companyForm.bci_name" placeholder="最多支持12个字" maxlength="12">
                </el-input>
              </el-form-item>
            </el-col>
            <el-col :md="12">
              <el-form-item label="机构类型:" style="margin-left: 10px" prop="region" :rules="this.companyFormRules.region">
                <el-select v-model="value" placeholder="请选择机构类型" @change="changestate"
                  :disabled="this.companyForm.flag === '2'?true:false">
                  <el-option v-for="item in select" :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :md="24">
            <el-col :md="24">
              <el-form-item label="机构编号:" prop="bci_code" :rules="this.companyFormRules.bci_code">
                <el-input :value="companyForm.bci_code|format" placeholder="选择下方机构类型自动生成，无需手工填写" disabled></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="24">
            <!--            v-show="this.pri_detail.currentTreeNode.type !== '0' && this.pri_detail.currentTreeNode !== ''"-->
            <el-col :md="12">
              <el-form-item label="行政区选择:" prop="loc" :rules="this.companyFormRules.loc">
                <div id="ssx">
                  <el-cascader size="large" :options="options" v-model="selectedOptions" @change="selectPCAChange">
                  </el-cascader>
                </div>
              </el-form-item>
            </el-col>
            <el-col :md="12">
              <el-form-item label="联系人所在部门:" prop="bci_department" :rules="this.companyFormRules.bci_department">
                <el-input v-model="companyForm.bci_department" placeholder="最多支持8个字" maxlength="8"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="24">

            <el-col :md="12">
              <el-form-item label="联系人:" prop="bci_contacts" :rules="this.companyFormRules.bci_contacts">
                <el-input v-model="companyForm.bci_contacts" placeholder="最多支持8个字" maxlength="8"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="12">
              <el-form-item label="联系人电话:" prop="bci_phone" :rules="this.companyFormRules.bci_phone">
                <el-input v-model="companyForm.bci_phone" placeholder="最多支持11位数字" maxlength="11"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="24">
              <el-form-item label="地址:" prop="bci_address" :rules="this.companyFormRules.bci_address">
                <el-input type="textarea" placeholder="最多支持30个字" :rows="2" v-model="companyForm.bci_address"
                  maxlength="30"></el-input>
              </el-form-item>
            </el-col>
          </el-row>


        </el-form>
        <!--form end-->
        <div slot="footer" class="dialog-footer">
          <el-button @click="resetForm('companyFormRef')">重 置</el-button>
          <el-button @click="cancelForm('companyFormRef')">取 消</el-button>
          <el-button type="primary" @click="submitForm('companyFormRef')">确 定</el-button>
        </div>
      </el-dialog>

      <!--小程序配置-->
      <el-dialog :visible.sync="Applets_configuration_dialog" :close-on-press-escape="false" width="800px"
        :close-on-click-modal="false" :show-close="false">
        <div slot="title" style="text-align: left;font-size:20px;font-weight: bold;">
          <span>{{ this.tableDialgTitle }}</span>
        </div>
        <!--form start-->
        <el-form  class="account-formitem" label-position="left" :model="appForm" ref="appForm" label-width="125px"
          style="text-align: left;font-size:20px;font-weight: bold;">
          <el-row :md="24" style="display: flex;justify-content: space-between;"> 
            <el-col :md="11">
              <el-form-item label="MerAppId:" prop="MerAppId">
                <el-input  v-model="appForm.MerAppId"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="11">
              <el-form-item label="MerAppSecret:" prop="MerAppSecret">
                <el-input  v-model="appForm.MerAppSecret"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :md="24"  style="display: flex;justify-content: space-between;">
            <el-col :md="11">
              <el-form-item label="BankAppId:" prop="BankAppId">
                <el-input  v-model="appForm.BankAppId"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="11">
              <el-form-item label="BankAppSecret:" prop="BankAppSecret">
                <el-input v-model="appForm.BankAppSecret"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :md="24"  style="display: flex;justify-content: space-between;">
            <el-col :md="11">
              <el-form-item label="UserAppId:" prop="UserAppId">
                <el-input  v-model="appForm.UserAppId"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="11">
              <el-form-item label="UserAppSecret:" prop="UserAppSecret">
                <el-input  v-model="appForm.UserAppSecret"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <!--form end-->
        <div slot="footer" class="dialog-footer">
          <el-button @click="applets_cancel()">取 消</el-button>
          <el-button type="primary" @click="applets_submit()">确 定</el-button>
        </div>
      </el-dialog>

      <!--微信支付配置-->
      <el-dialog :visible.sync="wechat_pay_dialog" :close-on-press-escape="false" :close-on-click-modal="false"
        :show-close="false" width="800px">
        <div slot="title" style="text-align: left;font-size:20px;font-weight: bold;">
          <span>{{ this.tableDialgTitle }}</span>
        </div>
        <!--form start-->
        <el-form class="account-formitem" label-position="left" :model="wechatForm" ref="wechatForm" label-width="130px"
          style="text-align: left;font-size:20px;font-weight: bold;">
          <el-row :md="24" style="display: flex;justify-content: space-between;">
            <el-col :md="11">
              <el-form-item label="MerAppId:" prop="MerAppId">
                <el-input style="width: 220px" v-model="wechatForm.MerAppId"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="11">
              <el-form-item label="MerMchId:" prop="MerMchId">
                <el-input style="width: 220px" v-model="wechatForm.MerMchId"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :md="24" style="display: flex;justify-content: space-between;">
            <el-col :md="11">
              <el-form-item label="MerApiKey:" prop="MerApiKey">
                <el-input style="width: 220px" v-model="wechatForm.MerApiKey"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="11">
              <el-form-item label="MerCertPath:" prop="MerCertPath">
                <el-input style="width: 220px" v-model="wechatForm.MerCertPath"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :md="24" style="display: flex;justify-content: space-between;">
            <el-col :md="11" >
              <el-form-item label="UserAppId:" prop="UserAppId">
                <el-input style="width: 220px" v-model="wechatForm.UserAppId"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="11">
              <el-form-item label="UserMchId:" prop="UserMchId">
                <el-input style="width: 220px" v-model="wechatForm.UserMchId"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :md="24" style="display: flex;justify-content: space-between;">
            <el-col :md="11">
              <el-form-item label="UserApiKey:" prop="UserApiKey">
                <el-input style="width: 220px" v-model="wechatForm.UserApiKey"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="11">
              <el-form-item label="UserCertPath:" prop="UserCertPath">
                <el-input style="width: 220px" v-model="wechatForm.UserCertPath"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <!--form end-->
        <div slot="footer" class="dialog-footer">
          <el-button @click="wechat_pay_cancel()">取 消</el-button>
          <el-button type="primary" @click="wechat_pay_submit()">确 定</el-button>
        </div>
      </el-dialog>

      <!--银联支付配置-->
      <el-dialog :visible.sync="UnionPay_pay_dialog" :close-on-press-escape="false" :close-on-click-modal="false"
        :show-close="false" width="800px" >
        <div slot="title" style="text-align: left;font-size:20px;font-weight: bold;">
          <span>{{ this.tableDialgTitle }}</span>
        </div>
        <!--form start-->
        <el-form class="account-formitem" label-position="left" :model="UnionPayForm" ref="UnionPayForm"
          label-width="130px" style="text-align: left;font-size:20px;font-weight: bold;">
          <el-row :md="24" style="display: flex;justify-content: space-between;">
            <el-col :md="11">
              <el-form-item label="appId:" prop="MerAppId">
                <el-input style="width: 220px" v-model="UnionPayForm.appId"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="11">
              <el-form-item label="appKey:" prop="MerMchId">
                <el-input style="width: 220px" v-model="UnionPayForm.appKey"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :md="24" style="display: flex;justify-content: space-between;">
            <el-col :md="11">
              <el-form-item label="md5Key:" prop="MerApiKey">
                <el-input style="width: 220px" v-model="UnionPayForm.md5Key"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="11">
              <el-form-item label="instMid:" prop="MerCertPath">
                <el-input style="width: 220px" v-model="UnionPayForm.instMid"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :md="24" style="display: flex;justify-content: space-between;">
            <el-col :md="11">
              <el-form-item label="mid:" prop="UserAppId">
                <el-input style="width: 220px" v-model="UnionPayForm.mid"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="11">
              <el-form-item label="tid:" prop="UserMchId">
                <el-input style="width: 220px" v-model="UnionPayForm.tid"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :md="24" style="display: flex;justify-content: space-between;">
            <el-col :md="11">
              <el-form-item label="msgId:" prop="UserApiKey">
                <el-input style="width: 220px" v-model="UnionPayForm.msgId"></el-input>
              </el-form-item>
            </el-col>
            <el-col :md="11">
            </el-col>
          </el-row>
        </el-form>
        <!--form end-->
        <div slot="footer" class="dialog-footer">
          <el-button @click="unionPay_payment_cancel()">取 消</el-button>
          <el-button type="primary" @click="unionPay_pay_submit()">确 定</el-button>
        </div>
      </el-dialog>

      <!--生成二维码-->
      <el-dialog :visible.sync="qr_code_dialog" :close-on-press-escape="false" :close-on-click-modal="false"
        :show-close="false">
        <div slot="title" style="text-align: left;font-size:20px;font-weight: bold;">
          <span>{{ this.tableDialgTitle }}</span>
        </div>
        <div id="qrCode" style="height: 500px">
          <el-image :src="this.filename"></el-image>
          <div style="margin-top: 10px;font-size: 20px;font-weight: bold;">{{ this.qr_code_name }}</div>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button @click="qr_code_cancel()">取 消</el-button>
          <el-button type="primary" @click="qr_code_submit()">保存</el-button>
        </div>
      </el-dialog>

      <!-- 新增商户弹框 -->
      <el-dialog :show-close="false" :close-on-press-escape="false" :close-on-click-modal="false"
      :visible.sync="newlyAdded.dialogVisible" width="600px">
        <div slot="title" style="text-align: left;font-size:20px;font-weight: bold;">
          <span>{{ '新增默认渠道商' }}</span>
        </div>
        <div class="box1 test-5">
          <div slot="title" style="text-align: left;font-size:18px;font-weight: bold;margin-left: 10px;">
            <span>基本信息</span>
          </div>
          <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="160px" class="demo-ruleForm">
            <el-row :gutter="24">
              <el-form-item label="商户名称:" prop="bmp_name" style="width: 85%;">
                <el-input :maxlength="20" placeholder="请输入内容,最多支持20个字" v-model="ruleForm.bmp_name" :disabled="true"></el-input>
              </el-form-item>
            </el-row>
            <el-row :gutter="24">
              <el-form-item label="银联子商户号:" prop="bmp_mid" style="width: 85%;">
                <el-input v-model="ruleForm.bmp_mid" placeholder="请输入银联子商户号" :disabled="true"></el-input>
              </el-form-item>
            </el-row>
            <el-row :gutter="24">
              <el-form-item label="联系人" prop="bmp_contacts" style="width: 85%;">
                <el-input placeholder="请输入内容,最多支持11个字" v-model="ruleForm.bmp_contacts" :disabled="true"></el-input>
              </el-form-item>
            </el-row>
            <el-row :gutter="24">
              <el-form-item label="联系电话:" prop="bmp_phone" style="width: 85%;">
                <el-input :maxlength="11" v-model="ruleForm.bmp_phone" :disabled="true"></el-input>
              </el-form-item>
            </el-row>
          </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="newlyAdded.dialogVisible = false">关 闭</el-button>
          <el-button type="primary" @click="submitData('ruleForm')">确 定</el-button>
        </span>
      </el-dialog >
      <!-- 系统库背景图片弹窗 -->
      <el-dialog :visible.sync="shareShow" width="1200px">
        <div class="shareNav">
          <div>系统库</div>
          <el-button type="primary" style="width: 120px;height: 40px;" @click="deleteImg">删除</el-button>
        </div>
        <shareBackground @vueCrroper="vueCrroper" ref="shareRef" :type="1" :shareMsg='shareMsg' :shareIndex='shareIndex' style="width: 100%;height: 500px;display: block;"/>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="shareShow = false">保 存</el-button>
        </span>
      </el-dialog>
      <!-- 系统库背景图片新增裁剪 -->
      <el-dialog :visible.sync="vueCrroperShow" >
        <vueCrroper @vueCrroper="vueCrroper" style="width: 100%;height: 500px;display: block;" :fixedNumber="[9,16]"/>
      </el-dialog>

      <!--加盟商户来源管理-->
      <el-dialog :visible.sync="franchised_merchant_dialog" :close-on-press-escape="false" :close-on-click-modal="false"
        :show-close="false">
        <div slot="title" style="text-align: left;font-size:20px;font-weight: bold;">
          <span>{{ this.tableDialgTitle }}</span>
        </div>
        <div style="min-height: 200px">
          <el-table :data="franchised_merchant_data" @sort-change="sortColumn"
