    methods: {
      newlyAddedClick(value,msg){
        console.log('value==', msg,'---------------------------------------------------------');
        this.newlyAdded.dialogVisible = true
        this.newlyAdded.name = msg.bsi_address+'_渠道商'
        let that = this;
        this.objMsg = msg
        let _jsondata_ = {
          obj_bank_center_id: msg.bsi_center_id,
          obj_province_id: msg.bsi_province_id,
          obj_city_id: msg.bsi_city_id,
          obj_county_id: msg.bsi_county_id,
          obj_station_id: msg.bsi_station_id,
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        let post = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.cooperative_channel_providers_list_query,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        post.then((res) => {
          console.log("异步调用成功返回:" + JSON.stringify(res));
          if (res.data.status == 'success') {
            if (res.data.rows.length==0) {
              this.newlyAdded.show=true
            }else{
              this.newlyAdded.show=false
            }
            loading.close();
          } else {
            console.info("商户查询失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        })
      },
      submitData(formName) {
        if (this.newlyAdded.show) {
          this.submitForm1()
        }else{
          this.newlyAdded.dialogVisible = false
        }
      },
