      initAccOrgTreeData(data, data2) {
        console.log('initAccOrgTreeData:');
        // 判断用户类型
        if (this.customGlobal.userInfoExt.cui_type === '0' && this.customGlobal.userInfoExt.cui_org_type === '0') {
          console.log('initAccOrgTreeData:1');
