      /** 将用户的机构权限分类放到数组中2  */
      selectOrgIds(row, id) {
        for (let i = 0; i < row.length; i++) {
          if (row[i].id === id) {
            if (row[i].type >= this.customGlobal.userInfoExt.cui_type) {
              switch (row[i].type) {
                case '1':
                  this.obj_bank_center_id.push(row[i].id);
                  break
                case '2':
                  this.obj_province_id.push(row[i].id);
                  break
                case '3':
                  this.obj_city_id.push(row[i].id);
                  break
                case '4':
                  this.obj_county_id.push(row[i].id);
                  break
                case '5':
                  this.obj_station_id.push(row[i].id);
                  break
              }
            }
          }
          if (row[i].detail) {
            this.selectOrgIds(row[i].detail, id);
          }
        }

      },
