      handleDeleteRow(index, row) {
        // let cur_id = this.pri_detail.currentTreeNode.orgid;/*当前子节点的orgid*/
        let cur_new_id = row.organization_uuid;/*表单中的code值*/
        // let cur_new_caption = this.companyForm.bci_name;/*新的名字*/
