                Bus.$emit('globalMsgBank', { curid: cur_new_id, target: {}, method: '3' });//删除结构树
                console.log('行系对象:' + this.obj_bank_center_id)
                console.log('省行对象:' + this.obj_province_id)
                console.log('市行对象:' + this.obj_city_id)
                console.log('区县行对象:' + this.obj_county_id)
                console.log('网点对象:' + this.obj_station_id)


                if (cur_type === '1') {
                  let pos = this.obj_bank_center_id.indexOf(cur_new_id);//位置
                  if (pos != -1) {
                    this.obj_bank_center_id.splice(pos, 1);
                    console.info(" this.obj_bank_center_id:" + JSON.stringify(this.obj_bank_center_id));
                  }

                  // for(let i = 0; i<=this.obj_bank_center_id.length;i++){
                  //   if(this.obj_bank_center_id[i]===cur_new_id){
                  //     console.log('删除数组')
                  //     // this.obj_bank_center_id.removeArray(this.obj_bank_center_id,row.organization_uuid);
                  //   }
                  // }

                } else if (cur_type === '2') {
