                  }
                } else if (cur_type === '4') {
                  let pos = this.obj_county_id.indexOf(cur_new_id);
                  if (pos != -1) {
                    this.obj_county_id.splice(pos, 1);
                    console.log('this.obj_county_id:' + JSON.stringify(this.obj_county_id))
                  }
                } else if (cur_type === '5') {
