                    this.obj_station_id.splice(pos, 1);
                    console.log('this.obj_station_id:' + JSON.stringify(this.obj_station_id))
                  }
                }


                this.queryValveCurrentData();
                loading.close();
              } else {
                //  console.info("删除失败");
                loading.close();
                // this.$alert('登录已超时，请重新登录！', '', {
                //   confirmButtonText: '确定',
                //   callback: action => {
                //     //  console.info('data:'+JSON.stringify(res.data));
                //     if (res.data.msg === '请登录认证' && res.data.status === 'failed') {
                //       this.$router.replace({path: '/login'});
