          bbpi_partner_bank_uuid_list: that.bbpi_partner_bank_uuid_list,
        };
        // const loading = this.$loading({
        //   lock: true,
        //   text: '提交中...',
        //   target: '.main',
        //   spinner: 'el-icon-loading',
        //
        //   background: 'rgba(0, 0, 0, 0)'
        // });
        var d2 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.Franchise_outlets_add,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        d2.then((res) => {
          if (res.data.status == 'success') {
            this.franchised_merchant_dialog = false;
          } else {
            console.info("加盟商户来源管理添加失败");
            // loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          // loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '加盟商户来源管理添加出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          // loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '加盟商户来源管理添加出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },
      //加盟商户来源管理列表查询
      franchised_merchant_select() {
        let that = this;
        let _jsondata_ = {
          perPageSize: 100,
          currentPage: 1,
          obj_station_id: that.bank_id,
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        var d2 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.Franchise_scope_select,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        d2.then((res) => {
          if (res.data.status == 'success') {
            that.franchised_merchant_data = [];
            loading.close();
            that.franchised_merchant_data = res.data.rows;
            console.log('加盟商户来源管理:' + JSON.stringify(that.franchised_merchant_data));
            that.toggleSelection(that.franchised_merchant_data);

          } else {
            console.info("加盟商户来源管理列表查询失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '加盟商户来源管理列表查询出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '加盟商户来源管理列表查询出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },
    }
  }
</script>

